import React, { useState, useRef, useEffect } from 'react';
import {
  Box, Paper, Typography, Button, CircularProgress, Tooltip, IconButton, Slider
} from '@mui/material';
import { getCurrentWeather, CurrentWeather } from '../services/weatherService';

// Define the window interface extension for Cesium global object
declare global {
  interface Window {
    Cesium: any;
  }
}

// Cesium Ion access token from environment variables
const CESIUM_ACCESS_TOKEN = process.env.REACT_APP_CESIUM_ACCESS_TOKEN || '9ff13709-5bab-46ad-a792-bba5db573d07';

// Weather layer icons (using emoji for now)
const LAYER_ICONS: Record<string, string> = {
  'Satellite': 'ðŸ›°ï¸',
  'Live': 'âœ“',
  'HD': 'ðŸ”',
  'Radar': 'ðŸ“¡',
  'Precipitation': 'ðŸŒ§ï¸',
  'Wind': 'ðŸ’¨',
  'Temperature': 'ðŸŒ¡ï¸',
  'Humidity': 'ðŸ’§',
  'Pressure': 'â­•',
};

const EarthViewerPage: React.FC = () => {
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [activeLayer, setActiveLayer] = useState('Satellite');
  const [weatherData, setWeatherData] = useState<CurrentWeather | null>(null);
  const [weatherLoading, setWeatherLoading] = useState(false);
  const [weatherError, setWeatherError] = useState<string | null>(null);
  const [selectedLocation, setSelectedLocation] = useState<{lat: number, lon: number, name: string} | null>(null);
  const [isPlaying, setIsPlaying] = useState(false);
  
  const cesiumContainerRef = useRef<HTMLDivElement>(null);
  const viewerRef = useRef<any>(null);
  const weatherEntityRef = useRef<any>(null);
  
  // Function to check if Cesium is loaded
  const isCesiumLoaded = () => {
    return typeof window.Cesium !== 'undefined';
  };
  
  // Function to check if WebGL is supported
  const isWebGLSupported = () => {
    try {
      const canvas = document.createElement('canvas');
      return (
        window.WebGLRenderingContext &&
        (canvas.getContext('webgl') || canvas.getContext('experimental-webgl'))
      );
    } catch (e) {
      return false;
    }
  };
  
  // Function to fetch weather data for a specific location
  const fetchWeatherData = async (lat: number, lon: number, locationName: string = 'Selected Location') => {
    if (weatherLoading) return;
    
    setWeatherLoading(true);
    setWeatherError(null);
    
    try {
      console.log(`Fetching weather data for ${locationName} (${lat}, ${lon})`);
      const data = await getCurrentWeather(lat, lon);
      setWeatherData(data);
      setSelectedLocation({ lat, lon, name: locationName });
      
      // If we have a Cesium viewer, add a weather marker at this location
      if (viewerRef.current && !viewerRef.current.isDestroyed()) {
        addWeatherMarker(lat, lon, data);
      }
    } catch (error) {
      console.error('Error fetching weather data:', error);
      setWeatherError('Failed to fetch weather data. Please try again.');
    } finally {
      setWeatherLoading(false);
    }
  };
  
  // Function to add a weather marker to the map
  const addWeatherMarker = (lat: number, lon: number, weather: CurrentWeather) => {
    const viewer = viewerRef.current;
    if (!viewer || viewer.isDestroyed()) return;
    
    // Remove existing weather entity if it exists
    if (weatherEntityRef.current && viewer.entities.contains(weatherEntityRef.current)) {
      viewer.entities.remove(weatherEntityRef.current);
    }
    
    // Get weather icon URL based on weather code
    const iconUrl = getWeatherIconUrl(weather.weathercode);
    
    // Create a new entity for the weather marker
    weatherEntityRef.current = viewer.entities.add({
      position: window.Cesium.Cartesian3.fromDegrees(lon, lat),
      billboard: {
        image: iconUrl,
        width: 32,
        height: 32,
        verticalOrigin: window.Cesium.VerticalOrigin.BOTTOM,
        disableDepthTestDistance: Number.POSITIVE_INFINITY
      },
      label: {
        text: `${Math.round(weather.temperature)}Â°C`,
        font: '14px sans-serif',
        fillColor: window.Cesium.Color.WHITE,
        outlineColor: window.Cesium.Color.BLACK,
        outlineWidth: 2,
        style: window.Cesium.LabelStyle.FILL_AND_OUTLINE,
        verticalOrigin: window.Cesium.VerticalOrigin.TOP,
        pixelOffset: new window.Cesium.Cartesian2(0, 5),
        disableDepthTestDistance: Number.POSITIVE_INFINITY
      }
    });
    
    // Fly to the weather location
    viewer.camera.flyTo({
      destination: window.Cesium.Cartesian3.fromDegrees(lon, lat, 1000000),
      duration: 2
    });
  };
  
  // Helper function to get weather icon URL based on weather code
  const getWeatherIconUrl = (code: number): string => {
    // Map weather codes to icon names
    let iconName = 'unknown';
    
    if (code === 0) iconName = 'clear-day';
    else if (code === 1 || code === 2) iconName = 'partly-cloudy-day';
    else if (code === 3) iconName = 'cloudy';
    else if (code === 45 || code === 48) iconName = 'fog';
    else if (code >= 51 && code <= 57) iconName = 'drizzle';
    else if (code >= 61 && code <= 67) iconName = 'rain';
    else if (code >= 71 && code <= 77) iconName = 'snow';
    else if (code >= 80 && code <= 82) iconName = 'rain';
    else if (code >= 85 && code <= 86) iconName = 'snow';
    else if (code >= 95) iconName = 'thunderstorm';
    
    // Return a placeholder URL - in a real app, you would use actual weather icons
    return `https://cdn.weatherapi.com/weather/64x64/day/${iconName}.png`;
  };
  
  // Function to switch between different imagery layers
  const switchImageryLayer = (layerName: string) => {
    if (!viewerRef.current || viewerRef.current.isDestroyed()) return;
    
    setActiveLayer(layerName);
    
    const viewer = viewerRef.current;
    const imageryLayers = viewer.imageryLayers;
    
    // Remove all existing layers except the base layer
    while (imageryLayers.length > 1) {
      imageryLayers.remove(imageryLayers.get(1));
    }
    
    // Add the selected layer
    try {
      switch (layerName) {
        case 'Satellite':
          // Use Bing Maps Aerial imagery
          imageryLayers.addImageryProvider(
            new window.Cesium.IonImageryProvider({
              assetId: 2 // Bing Maps Aerial
            })
          );
          break;
          
        case 'Live':
          // Use Bing Maps Aerial with Labels imagery
          imageryLayers.addImageryProvider(
            new window.Cesium.IonImageryProvider({
              assetId: 3 // Bing Maps Aerial with Labels
            })
          );
          break;
          
        case 'HD':
          // Use high-definition imagery
          imageryLayers.addImageryProvider(
            new window.Cesium.IonImageryProvider({
              assetId: 4 // World Imagery
            })
          );
          break;
          
        case 'Radar':
          // Fetch radar data if we have a location
          if (selectedLocation) {
            // In a real app, you would fetch and display actual radar data
            // For now, we'll use the simulated layer
            const radarLayer = imageryLayers.addImageryProvider(
              new window.Cesium.IonImageryProvider({
                assetId: 2 // Bing Maps Aerial
              })
            );
            radarLayer.alpha = 0.7;
          } else {
            // Default to India if no location selected
            fetchWeatherData(20.5937, 78.9629, 'India');
          }
          break;
          
        case 'Precipitation':
          // Fetch precipitation data if we have a location
          if (selectedLocation) {
            // In a real app, you would fetch and display actual precipitation data
            // For now, we'll use the simulated layer
            const precipLayer = imageryLayers.addImageryProvider(
              new window.Cesium.IonImageryProvider({
                assetId: 2 // Bing Maps Aerial
              })
            );
            precipLayer.alpha = 0.7;
            precipLayer.brightness = 1.0;
            precipLayer.contrast = 1.0;
            precipLayer.hue = 0.5; // Blue tint
            precipLayer.saturation = 2.0;
          } else {
            // Default to India if no location selected
            fetchWeatherData(20.5937, 78.9629, 'India');
          }
          break;
          
        case 'Wind':
          // Fetch wind data if we have a location
          if (selectedLocation) {
            // In a real app, you would fetch and display actual wind data
            // For now, we'll use the simulated layer
            const windLayer = imageryLayers.addImageryProvider(
              new window.Cesium.IonImageryProvider({
                assetId: 2 // Bing Maps Aerial
              })
            );
            windLayer.alpha = 0.7;
          } else {
            // Default to India if no location selected
            fetchWeatherData(20.5937, 78.9629, 'India');
          }
          break;
          
        case 'Temperature':
          // Fetch temperature data if we have a location
          if (selectedLocation) {
            // In a real app, you would fetch and display actual temperature data
            // For now, we'll use the simulated layer
            const tempLayer = imageryLayers.addImageryProvider(
              new window.Cesium.IonImageryProvider({
                assetId: 3 // Bing Maps Aerial with Labels
              })
            );
            tempLayer.alpha = 0.5;
            tempLayer.brightness = 1.0;
            tempLayer.contrast = 1.0;
            tempLayer.hue = 0.0; // Red tint
            tempLayer.saturation = 2.0;
          } else {
            // Default to India if no location selected
            fetchWeatherData(20.5937, 78.9629, 'India');
          }
          break;
          
        case 'Humidity':
          // Fetch humidity data if we have a location
          if (selectedLocation) {
            // In a real app, you would fetch and display actual humidity data
            // For now, we'll use the simulated layer
            const humidityLayer = imageryLayers.addImageryProvider(
              new window.Cesium.IonImageryProvider({
                assetId: 3 // Bing Maps Aerial with Labels
              })
            );
            humidityLayer.alpha = 0.5;
            humidityLayer.brightness = 1.0;
            humidityLayer.contrast = 1.0;
            humidityLayer.hue = 0.33; // Green tint
            humidityLayer.saturation = 2.0;
          } else {
            // Default to India if no location selected
            fetchWeatherData(20.5937, 78.9629, 'India');
          }
          break;
          
        case 'Pressure':
          // Fetch pressure data if we have a location
          if (selectedLocation) {
            // In a real app, you would fetch and display actual pressure data
            // For now, we'll use the simulated layer
            const pressureLayer = imageryLayers.addImageryProvider(
              new window.Cesium.IonImageryProvider({
                assetId: 3 // Bing Maps Aerial with Labels
              })
            );
            pressureLayer.alpha = 0.5;
            pressureLayer.brightness = 1.0;
            pressureLayer.contrast = 1.0;
            pressureLayer.hue = 0.83; // Purple tint
            pressureLayer.saturation = 2.0;
          } else {
            // Default to India if no location selected
            fetchWeatherData(20.5937, 78.9629, 'India');
          }
          break;
          
        default:
          // Default to live view
          break;
      }
    } catch (error) {
      console.error(`Error switching to ${layerName} layer:`, error);
    }
  };
  
  // Initialize Cesium viewer when component mounts
  useEffect(() => {
    // Skip initialization if Cesium is not loaded
    if (!isCesiumLoaded()) {
      setError('Cesium library not loaded. Please check your internet connection and reload the page.');
      setLoading(false);
      return;
    }
    
    // Check if WebGL is supported
    if (!isWebGLSupported()) {
      setError('WebGL is not supported or enabled in your browser. Please enable WebGL or try a different browser.');
      setLoading(false);
      return;
    }
    
    // Make sure the container is visible and properly sized
    if (cesiumContainerRef.current) {
      cesiumContainerRef.current.style.width = '100%';
      cesiumContainerRef.current.style.height = '100%';
      cesiumContainerRef.current.style.position = 'absolute';
      cesiumContainerRef.current.style.top = '0';
      cesiumContainerRef.current.style.left = '0';
    }
    
    // Set Cesium Ion access token
    window.Cesium.Ion.defaultAccessToken = CESIUM_ACCESS_TOKEN;
    
    // Initialize the Cesium Viewer
    try {
      // Make sure we don't have an existing viewer
      if (viewerRef.current && !viewerRef.current.isDestroyed()) {
        viewerRef.current.destroy();
      }
      
      console.log('Initializing Cesium viewer');
      
      // Create the Cesium viewer
      const viewer = new window.Cesium.Viewer(cesiumContainerRef.current!, {
        terrainProvider: window.Cesium.createWorldTerrain(),
        imageryProvider: new window.Cesium.IonImageryProvider({
          assetId: 2 // Bing Maps Aerial
        }),
        baseLayerPicker: false,
        geocoder: false,
        homeButton: false,
        sceneModePicker: false,
        navigationHelpButton: false,
        animation: false,
        timeline: false,
        fullscreenButton: false,
        vrButton: false,
        selectionIndicator: false,
        infoBox: false,
        requestRenderMode: false,
        maximumRenderTimeChange: Infinity,
        targetFrameRate: 60
      });
      
      // Store the viewer reference
      viewerRef.current = viewer;
      
      // Set initial camera position to focus on India
      viewer.camera.flyTo({
        destination: window.Cesium.Cartesian3.fromDegrees(78.9629, 20.5937, 8000000),
        orientation: {
          heading: window.Cesium.Math.toRadians(0.0),
          pitch: window.Cesium.Math.toRadians(-90.0),
          roll: 0.0
        },
        duration: 2.0
      });
      
      // Add click event handler to get weather at clicked location
      viewer.screenSpaceEventHandler.setInputAction((click: any) => {
        const cartesian = viewer.camera.pickEllipsoid(
          click.position,
          viewer.scene.globe.ellipsoid
        );
        
        if (cartesian) {
          const cartographic = window.Cesium.Cartographic.fromCartesian(cartesian);
          const lon = window.Cesium.Math.toDegrees(cartographic.longitude);
          const lat = window.Cesium.Math.toDegrees(cartographic.latitude);
          
          // Fetch weather for the clicked location
          fetchWeatherData(lat, lon, 'Selected Location');
        }
      }, window.Cesium.ScreenSpaceEventType.LEFT_CLICK);
      
      // Fetch weather for default location (India)
      fetchWeatherData(20.5937, 78.9629, 'India');
      
      // Set loading to false once viewer is initialized
      setLoading(false);
    } catch (error) {
      console.error('Error initializing Cesium viewer:', error);
      setError('Failed to initialize the Earth viewer. Please check your browser compatibility and try again.');
      setLoading(false);
    }
    
    // Cleanup function to destroy viewer when component unmounts
    return () => {
      if (viewerRef.current && !viewerRef.current.isDestroyed()) {
        viewerRef.current.destroy();
        viewerRef.current = null;
      }
    };
  }, []);
  
  // Handle zoom in
  const handleZoomIn = () => {
    if (viewerRef.current && !viewerRef.current.isDestroyed()) {
      const camera = viewerRef.current.camera;
      const cameraHeight = camera.positionCartographic.height;
      camera.zoomIn(cameraHeight * 0.2);
    }
  };
  
  // Handle zoom out
  const handleZoomOut = () => {
    if (viewerRef.current && !viewerRef.current.isDestroyed()) {
      const camera = viewerRef.current.camera;
      const cameraHeight = camera.positionCartographic.height;
      camera.zoomOut(cameraHeight * 0.2);
    }
  };
  
  // Toggle play/pause
  const togglePlayPause = () => {
    setIsPlaying(!isPlaying);
  };
  
  return (
    <Box sx={{ height: '100vh', width: '100vw', overflow: 'hidden', position: 'relative', bgcolor: '#000' }}>
      {/* Cesium container - full screen with no borders */}
      <div
        ref={cesiumContainerRef}
        style={{ width: '100%', height: '100%', position: 'absolute' }}
      />
      
      {/* Left vertical toolbar with weather layer toggles - styled like Zoom Earth */}
      {!loading && !error && (
        <Paper
          elevation={3}
          sx={{
            position: 'absolute',
            top: 60,
            left: 10,
            width: 180,
            bgcolor: 'rgba(33, 33, 33, 0.85)',
            color: 'white',
            borderRadius: 1,
            overflow: 'hidden',
            zIndex: 1000,
          }}
        >
          <Box sx={{ 
            p: 1.5, 
            display: 'flex', 
            alignItems: 'center', 
            justifyContent: 'center',
            borderBottom: '1px solid rgba(255,255,255,0.1)'
          }}>
            <Box sx={{ 
              width: 30, 
              height: 30, 
              borderRadius: '50%', 
              background: 'radial-gradient(circle, #4fc3f7, #0277bd)',
              mr: 1.5,
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center'
            }}>
              <Typography variant="body1" sx={{ color: 'white', fontWeight: 'bold' }}>Z</Typography>
            </Box>
            <Typography variant="subtitle1" sx={{ fontWeight: 'bold', letterSpacing: 1 }}>
              ZOOM EARTH
            </Typography>
          </Box>
          
          <Box sx={{ 
            p: 1, 
            borderBottom: '1px solid rgba(255,255,255,0.1)',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'space-between'
          }}>
            <Typography variant="subtitle2" sx={{ fontWeight: 'bold' }}>
              WEATHER MAPS
            </Typography>
            <IconButton size="small" sx={{ color: 'white', p: 0 }}>
              â–²
            </IconButton>
          </Box>
          
          <Box sx={{ p: 0 }}>
            {[
              { name: 'Satellite', value: 'Satellite' },
              { name: 'Live', value: 'Live' },
              { name: 'HD', value: 'HD' },
              { name: 'Radar', value: 'Radar' },
              { name: 'Precipitation', value: 'Precipitation' },
              { name: 'Wind', value: 'Wind' },
              { name: 'Temperature', value: 'Temperature' },
              { name: 'Humidity', value: 'Humidity' },
              { name: 'Pressure', value: 'Pressure' }
            ].map((layer) => (
              <Button
                key={layer.value}
                fullWidth
                size="small"
                variant="text"
                onClick={() => switchImageryLayer(layer.value)}
                sx={{
                  justifyContent: 'flex-start',
                  py: 1.2,
                  px: 2,
                  borderRadius: 0,
                  bgcolor: activeLayer === layer.value ? 'rgba(70, 70, 70, 0.8)' : 'transparent',
                  color: 'white',
                  '&:hover': {
                    bgcolor: 'rgba(70, 70, 70, 0.5)'
                  },
                  textTransform: 'none',
                  fontWeight: activeLayer === layer.value ? 'bold' : 'normal',
                  borderLeft: activeLayer === layer.value ? '3px solid #4fc3f7' : '3px solid transparent'
                }}
              >
                <span style={{ marginRight: '12px', width: '16px', display: 'inline-block', textAlign: 'center' }}>
                  {LAYER_ICONS[layer.value]}
                </span>
                {layer.name}
              </Button>
            ))}
          </Box>
        </Paper>
      )}
      
      {/* Right vertical mini toolbar - styled like Zoom Earth */}
      {!loading && !error && (
        <Box
          sx={{
            position: 'absolute',
            top: '50%',
            right: 10,
            transform: 'translateY(-50%)',
            display: 'flex',
            flexDirection: 'column',
            gap: 1,
            zIndex: 1000,
          }}
        >
          <Paper
            elevation={3}
            sx={{
              bgcolor: 'rgba(33, 33, 33, 0.85)',
              color: 'white',
              borderRadius: 28,
              overflow: 'hidden',
              display: 'flex',
              flexDirection: 'column',
              p: 1,
            }}
          >
            <Tooltip title="Search" placement="left">
              <IconButton size="small" sx={{ color: 'white', mb: 1.5, width: 40, height: 40 }}>
                ðŸ”
              </IconButton>
            </Tooltip>
            
            <Tooltip title="Settings" placement="left">
              <IconButton size="small" sx={{ color: 'white', mb: 1.5, width: 40, height: 40 }}>
                âš™ï¸
              </IconButton>
            </Tooltip>
            
            <Tooltip title="Info" placement="left">
              <IconButton size="small" sx={{ color: 'white', mb: 1.5, width: 40, height: 40 }}>
                â„¹ï¸
              </IconButton>
            </Tooltip>
            
            <Tooltip title="Share" placement="left">
              <IconButton size="small" sx={{ color: 'white', mb: 1.5, width: 40, height: 40 }}>
                ðŸ“¤
              </IconButton>
            </Tooltip>
            
            <Tooltip title="Measure" placement="left">
              <IconButton size="small" sx={{ color: 'white', mb: 1.5, width: 40, height: 40 }}>
                ðŸ“
              </IconButton>
            </Tooltip>
            
            <Tooltip title="Crosshair" placement="left">
              <IconButton size="small" sx={{ color: 'white', mb: 1.5, width: 40, height: 40 }}>
                âŠ•
              </IconButton>
            </Tooltip>
          </Paper>
          
          <Paper
            elevation={3}
            sx={{
              bgcolor: 'rgba(33, 33, 33, 0.85)',
              color: 'white',
              borderRadius: 28,
              overflow: 'hidden',
              display: 'flex',
              flexDirection: 'column',
              p: 1,
              mt: 2
            }}
          >
            <Tooltip title="Zoom In" placement="left">
              <IconButton 
                size="small" 
                sx={{ color: 'white', mb: 1.5, width: 40, height: 40 }}
                onClick={handleZoomIn}
              >
                +
              </IconButton>
            </Tooltip>
            
            <Tooltip title="Zoom Out" placement="left">
              <IconButton 
                size="small" 
                sx={{ color: 'white', width: 40, height: 40 }}
                onClick={handleZoomOut}
              >
                -
              </IconButton>
            </Tooltip>
          </Paper>
        </Box>
      )}
      
      {/* Loading indicator */}
      {loading && (
        <Box
          sx={{
            position: 'absolute',
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            bgcolor: 'rgba(0, 0, 0, 0.7)',
            zIndex: 2000,
          }}
        >
          <Box sx={{ textAlign: 'center', color: 'white' }}>
            <CircularProgress color="inherit" size={60} thickness={4} />
            <Typography variant="h6" sx={{ mt: 2 }}>
              Loading Earth Viewer...
            </Typography>
          </Box>
        </Box>
      )}
      
      {/* Error message */}
      {!loading && error && (
        <Box
          sx={{
            position: 'absolute',
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            bgcolor: 'rgba(0, 0, 0, 0.7)',
            zIndex: 2000,
            p: 3,
          }}
        >
          <Paper sx={{ p: 3, maxWidth: 500, bgcolor: 'rgba(50, 50, 50, 0.9)', color: 'white' }}>
            <Typography variant="h5" sx={{ mb: 2, color: '#ff5252' }}>
              Error Loading Earth Viewer
            </Typography>
            <Typography variant="body1" sx={{ mb: 2 }}>
              {error}
            </Typography>
            <Button
              variant="contained"
              color="primary"
              onClick={() => window.location.reload()}
            >
              Reload Page
            </Button>
          </Paper>
        </Box>
      )}
    </Box>
  );
};

export default EarthViewerPage;
